<template>
    <div class="mypage-container">
        <div class="mypage-title pc-element">비밀번호 변경</div>
        <form novalidate @submit.prevent="validate">
            <ul class="input-ul" style="padding-top:30px;">
                <li>
                    <div class="label">현재 비밀번호</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                ref="nowPass"
                                type="password"
                                :rules="[() => !!nowPass || '현재 비밀번호를 입력하세요']"
                                v-model="nowPass"/>
                    </div>
                </li>
                <li>
                    <div class="label">새 비밀번호</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                ref="newPass"
                                type="password"
                                :rules="[checkNewPass]"
                                v-model="newPass"/>
                    </div>
                </li>
                <li>
                    <div class="label">새 비밀번호 확인</div>
                    <div class="input-box">
                        <v-text-field
                                outlined
                                ref="reNewPass"
                                type="password"
                                :rules="[checkReNewPass]"
                                v-model="reNewPass"/>
                    </div>
                </li>
            </ul>
            <div class="button-row">
                <v-btn type="submit" class="blackButton">
                    <span>저장</span>
                </v-btn>
            </div>
        </form>
        <div v-if="role != 'ROLE_ADMIN'">
            <div class="mypage-title" style="border-bottom:none;border-top:solid 1px #ddd;padding-top:20px;">계정 삭제</div>
            <div class="out-text" v-html="outContent"></div>
            <div class="check-box">
                <v-checkbox
                        color="#000"
                        label="계정 삭제에 관한 정책을 읽고 이에 동의합니다."
                        value="yes"
                        v-model="uoutAgree"/>
            </div>

            <div class="button-row">
                <v-btn type="button" class="blackButton" @click="memberOut()">
                    <span>회원탈퇴</span>
                </v-btn>
            </div>
        </div>
    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_UPDATE_MYPAGE_PASSWORD,
        API_URL_UPDATE_MYPAGE_OUT,
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import {mapGetters} from "vuex";
    import JQ from "jquery"

    export default {
        name: 'memberPassword',
        computed: {
            ...mapGetters('config', ["getAgree", "getSettingActive"]),
            form() {
                return {
                    nowPass: this.nowPass,
                    newPass: this.newPass,
                    reNewPass: this.reNewPass,
                }
            },

        },
        watch: {
            getSettingActive(val) {
                if (val) {
                    this.setAgree();
                }
            }
        },
        data() {
            return {
                nowPass: '',
                newPass: '',
                reNewPass: '',
                uoutAgree: '',
                serverFlag: false,
                outContent: '',
                role: '',
            };
        },
        created() {
            const member = AUTH.getSession();
            if (!member || !member?.memberInfo) {
                this.$router.push('/');
            }
            if (this.getSettingActive) {
                this.setAgree();
            }
            this.role = member.memberInfo.role;

        },
        methods: {
            setAgree() {
                this.outContent = this.getAgree.out.content;


            },
            checkNewPass() {
                if (!this.newPass) return '새 비밀번호를 입력하세요'
                if (this.newPass.length < 6 || this.newPass.length > 20) return '비밀번호는 6~20자 사이로 입력하세요'

                let reg = /\d{1}/;
                let ret = reg.test(this.newPass)
                if (!ret) {
                    return '비밀번호는 한개이상의 숫자를 포함해야 합니다'
                }

                reg = /[a-zA-Z]/;
                ret = reg.test(this.newPass)
                if (!ret) {
                    return '비밀번호는 영문을 포함해야 합니다'
                }

                reg = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/;
                ret = reg.test(this.newPass)
                if (ret) {
                    return '비밀번호는 한글을 포함해서는 안됩니다'
                }
                return true;
            },
            checkReNewPass() {
                if (!this.reNewPass) return '새 비밀번호 확인를 입력하세요'
                if (this.reNewPass != this.newPass) return '새 비밀번호와 새 비밀번호 확인이 일치하지 않습니다'
                return true;
            },
            validate() {
                let formHasErrors = false;
                let frm = {};
                Object.keys(this.form).forEach(f => {
                    let error = this.$refs[f].validate(true)
                    if (!error) {
                        formHasErrors = true
                    } else {
                        frm[f] = this.form[f];
                    }
                })
                if (!formHasErrors) {
                    if (this.serverFlag) return;
                    this.$eventBus.$emit('overlay', 'open');
                    this.serverFlag = true;
                    apiService.post(API_URL_UPDATE_MYPAGE_PASSWORD, frm, (res) => {
                        this.serverFlag = false;
                        this.$eventBus.$emit('overlay', 'hide');
                        let result = res.status;
                        let params = {}
                        if (result == 'success') {
                            params = {
                                message: '비밀번호가 변경되었습니다',
                            };


                        } else {
                            params = {
                                message: '잘못된 현재 비밀번호 입니다',
                            };
                        }
                        this.$eventBus.$emit('modalOpen', params);


                    })

                }
            },
            memberOut() {
                let params = {};
                if(this.uoutAgree!='yes') {
                    params = {
                        message:'계정삭제에 관한 정책에 동의해주세요'
                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }

                params = {
                    type:'confirm',
                    message:'정말 회원탈퇴 하시겠습니까?',
                    doAction:()=>{
                        this.outActive()
                    }
                }
                this.$eventBus.$emit('modalOpen', params);
            },
            outActive() {
                if (this.serverFlag) return;
                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_UPDATE_MYPAGE_OUT, {}, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    let result = res.status;
                    let params = {}
                    if (result == 'success') {
                        params = {
                            message: '회원탈퇴 처리되었습니다',
                            doAction:()=>{
                                AUTH.logout();
                                location.href = '/';
                            }
                        };
                        this.$eventBus.$emit('modalOpen', params);
                    }



                })
            }
        },
        mounted() {

            JQ('.out-text p').css({
                'margin': 0,
                'font-size': '14px',
                'color': '#333',
                'font-weight': '500'
            });
        },
        updated() {

            JQ('.out-text p').css({
                'margin': 0,
                'font-size': '14px',
                'color': '#333',
                'font-weight': '500'
            });
        }

    }
</script>


<style lang="scss" scoped>
    .mypage-container {
        width: 100%;
        margin: 0;
        padding: 21px 24px;
        min-height: calc(100vh - 300px);
        background: #fff;

        .mypage-title {
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            border-bottom: solid 1px #333;
            padding-bottom: 10px;
        }

        .button-row {
            width: 86px;
            margin: 10px auto 40px auto;

            .blackButton {
                max-height: 38px !important;

                span {
                    font-size: 14px;
                }
            }

        }

        .out-text {

            font-size: 14px;
            color: #333;
            font-weight: 500;
            padding-bottom: 10px;

        }

        .check-box {
            margin-top: -10px;

            ::v-deep .v-label {
                font-size: 14px;
                color: #333;
                margin-left: -5px;
            }
        }

        .input-ul {
            padding: 0 20px;
            margin: 10px 0 0 0;

            li {
                display: flex;
                justify-content: space-between;

                .label {
                    min-width: 120px;
                    padding: 15px 0 0 0;
                    font-weight: 600;
                    color: #333;
                    font-size: 14px;

                }

                .input-box {
                    text-align: left;
                    width: 95%;

                }

                .radio-box {
                    text-align: left;
                    width: 95%;
                    padding-bottom: 10px;
                }

                .switch-box {
                    width: 95%;
                    display: flex;
                    justify-content: space-between;

                    .tip {
                        width: 99%;
                        font-size: 12px;
                        color: #333333;
                        padding-top: 15px;
                    }

                    .switch {
                        text-align: right;
                        min-width: 30px;
                        margin-top: -6px;
                    }
                }
            }
        }

    }
    @media screen and (max-width: 1259px) {
        .mypage-container {
            padding: 0;


            .input-ul {
                padding: 0;
                margin: 10px 0 0 0;

                li {
                    display: block;
                    .label {
                        padding:0 0 10px 0;
                    }

                    .input-box {
                        width:100%;

                    }


                }

            }

        }
    }
    @media screen and (max-width: 450px) {
        .mypage-container {
            padding: 0 20px;


        }
    }
</style>
